import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import keyboardUse from '../../../../images/keyboardUse.png';

export default class KeyboardUse extends Component {
    render() {
        return (
            <div style={{ marginTop: '5%', marginBottom: '5%' }}>
                <h1>Jak ukada palce na klawiaturze?</h1>
                <p>Nauczysz si tutaj poprawnie ukada  palce, eby pisa litery, numery i znaki specjalne jak najbardziej efektywnie.</p>
                <p>Podstawowymi klawiszami rozpoczynajcymi ukadanie palcw na klawiaturze s F i J, ktre posiadaj na sobie ma wypustke ktra je wskazuje. Przyciski te obsugujemy za pomoc <b>palcw wskazujcych</b></p>
                <p>Palce lewej rki powinny by uoone nad klawiszami A,S, D i F, natomiast palce prawej rki powinny znajdowa si nad przyciskami J, K, L i ;</p>
                <br />
                <p>S to przyciski wiersza gwnego. Jest to rzd gdzie twoje palce odpoczywaj, gdy nie s uywane. Pozwala to na szybsze pisanie poprzez moliwo szybszego
                dostepu do klawiszy dookoa.</p>
                <p>Kciuki powinny znajdowa si w powietrzu albo lekko dotykajc spacj.</p>
                <Image src={keyboardUse} alt="Podzia klawiatury" fluid></Image>
                <p>Na powyszym obrazku zaznaczony jest podzia klawiatury na rc, lecz za pomoc kolorw take na konkretne palce, ktre su do obsugiwania danego przycisku</p>
                <p><span style={{ color: 'DodgerBlue', textShadow: '1px 1px #000000' }}>Kolor niebieski</span> - kciuki</p>
                <p><span style={{ color: 'LimeGreen', textShadow: '1px 1px #000000' }}>Kolor zielony</span> - palce wskazujce</p>
                <p><span style={{ color: 'yellow', textShadow: '1px 1px #000000' }}>Kolor ty</span> - palce rodkowe</p>
                <p><span style={{ color: 'orange', textShadow: '1px 1px #000000' }}>Kolor pomaraczowy</span> - palce serdeczne</p>
                <p><span style={{ color: 'Tomato', textShadow: '1px 1px #000000' }}>Kolor czerwony</span> - mae palce</p>
            </div>
        )
    }
}
