import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import keyboardBlocks from '../../../../images/keyboard-blocks.png';
import './keyboardTips.css';


export default class KeyboardTips extends Component {
    render() {
        return (
            <div style={{ marginTop: '5%', marginBottom: '5%' }}>
                <h1>Budowa klawiatury</h1>
                <p>Klawiatura jest podzielona na kilka blokw klawiszy.</p>
                <Image src={keyboardBlocks} alt="Bloki klawiatury" fluid></Image>
                <br />
                <div className="keysDescription">
                    <p>Podstawowym blokiem s klawisze z literami, ktre pozwalaj na wprowadzanie tekstu. Znajduj si tam rwnie klawisze pozwalajce nawprowadzanie np. cyfr, nawiasw czy klawisz <b>Spacja</b>, ktra pozwala umieszcza odstpy midzy literami. Po prawej jak i lewej tego bloku znajduj si klawisze specjalne.</p>
                    <p><b>Backspace</b> pozwala na usunicie jedego znaku przed kursorem.</p>
                    <p><b>Shift</b> to klawisz, ktry pozwala na wprowadzenie wielkiej litery poprzez przytrzymanie go oraz nacinitej porzdanej litery.</p>
                    <p>Podobn funkcje peni klawisz <b>Capslock</b> jednak po jego wciniciu bdziemy wprowadza wielkie litery do momentu jego ponownego wcinicia.</p>
                    <p><b>Tab</b> pozwala na tworzenie wcicia poprzez przesuwajc kursor o okrelon odlego w prawo, lub na przemieszczanie si pomidzy elementami np. w tabeli.</p>
                    <p><b>Enter</b> suy do zatwierdzania wpisanego tekstu lub przechodzenia do nastpnej linii.</p>
                    <p><b>Alt</b> dziki jego uyciu moliwe jest wprowadzenie polskich znakw takich jak: , , , . Z poczeniu z innym klawiszem pozwala rwnie na uruchamianie innych funkcji systemu.</p>
                    <p><b>Blok strzaek</b> pozwala na poruszanie kursorem. Mog by uywane do tego celu zamiast myszki.</p>
                </div>
            </div>
        )
    }
}
