import React, { Component } from 'react'
import sitting from '../../../../images/sitting.jpg'
import { Image } from 'react-bootstrap';
import './positionTips.css';


export default class Position extends Component {
    render() {
        return (
            <div style={{marginTop:'5%', marginBottom:'5%'}}>
                <h2>Jak siedzie przy biurku?</h2>
                <p>
                    Czsto marginalizowan rzecz przy korzystaniu z komputera jest poprawne siedzenie. Odpowiednie ustawienie stanowiska pozwala na uzyskanie komfortu pracy oraz uchroni nas przed wieloma dolegliwociami.
                    <br />
                    Najbardziej popularnymi problemami s:
                <ul>
                        <li>sztywno karku</li>
                        <li>bl szyi</li>
                        <li>zesp cieni nadgarstka</li>
                        <li>zgarbienie plecw</li>
                    </ul>
                </p>
                <div className="howToSit">
                    <Image src={sitting} fluid />
                    <a className="designedBy" href="http://www.freepik.com">Designed by macrovector / Freepik</a>
                </div>
                <h1 style={{marginTop:"50px"}}>Krzeso</h1>
                <p>
                    Powinno posiada jak najwicej opcji regulacji po to by dostosowa je pod swoje ciao. Rwnie wan rzecz s podokietniki, ktre pozwol naszym rk przyjc optymaln pozycj. Powinny one by ustawione na wysokoci blatu.
                    Siedzisko naley ustawi w taki sposb by podczas pisania plecy byy oparte, a okcie pooone na podokietnikach. Jego wysoko powinna by regulowana w zakresie 40 a 50 cm w zalenoci od dugoci podudzia, kt zgicia w biodrach powinien wynosi okoo 90 stopni, podobnie jak w kolanach.
                </p>
                <br />
                <h1>Monitor</h1>
                <p>
                    Powinien by ustawiony przynajmniej w odlegoci 50 cm od twarzy, grna krawd powinna znajdowa si na lub pod lini oczu. Ekran powinno si odchyli do tyu o 5-10 stopni.
                    W monitorach stosuje si szereg technologii poprawiajcych komfort pracy. Przy kupnie na warto powici troche czasu na wybr odpowiedniego ekranu oferujcego najlepsze funkcj.
                </p>
                <br />
                <h1>Klawiatura</h1>
                <p>
                    W przypadku pracy na klawiaturach wysokoprofilowych, warto zastosowa podkadk pod nadgarstki, ktra pozwala na utrzymanie nadgarstkw w prawidowej pozycji. Wspczesne klawiatury czsto oferuj zintegrowan podkadke z klawiatur, lecz nie zawsze. Wtedy warto dokupi j osobo, w celu zapewnienia jak najwyszego komfortu.
                </p>
                <br/>
                <h1>Przerwy</h1>
                <p>
                    Nie naley siedzie zbyt dugo w tej samej pozycji. Powinno si zmienia swoj pozycje tak czsto jak jest to moliwe. Nieprzerwana praca przy monitorze powinna trwa nie duej ni 45 minut. Czste krtkie przerwy s lepsze dla plecw ni mniej dugich przerw. Daje to miniom szans na rozlunienie si.
                </p>
            </div>
        )
    }
}
